body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@tailwind base;
@tailwind components;
@tailwind utilities;

.camera-container {
  @apply h-[120vh] p-6; /* Decreased height */
  background: linear-gradient(135deg, white 0%, #764ba2 100%);
}

.camera-content {
  @apply p-4 flex flex-col items-center space-y-6 relative;
}

.card-header {
  @apply bg-gradient-to-r from-blue-600 to-indigo-600 p-6 text-left w-full flex justify-between items-center; /* Adjusted */
}

.header-title {
  @apply text-2xl font-bold text-white;
}

.webcam-container {
  @apply relative w-full max-w-2xl mx-auto; /* Moderate width */
}

.webcam-view {
  @apply w-full h-[350px] rounded-lg overflow-hidden shadow-lg; /* Smaller webcam view */
}

.camera-indicator {
  @apply absolute top-4 right-4 animate-pulse flex items-center space-x-2 bg-black bg-opacity-50 text-white text-sm px-3 py-1 rounded-full;
}

.capture-button {
  @apply flex items-center space-x-2 px-8 py-4 bg-blue-600 text-white rounded-lg shadow-lg hover:bg-blue-700 transform hover:scale-105 transition-all duration-300;
}

.retake-button {
  @apply flex items-center space-x-2 px-6 py-3 bg-red-500 text-white rounded-lg shadow-lg hover:bg-red-600 transition-colors duration-300;
}

.captured-image {
  @apply w-full h-96 object-cover rounded-lg shadow-md transition-transform duration-300;
}

.camera-card {
  @apply w-full max-w-2xl bg-white rounded-xl shadow-2xl overflow-hidden relative; 
}

.flip-camera-button {
  @apply absolute top-4 right-4 p-3 bg-white rounded-full shadow-lg border-2 border-blue-600 hover:bg-gray-100 transition-colors duration-300 flex items-center justify-center;
  z-index: 20;
}